import React, { useState, useEffect, useRef } from 'react';
import _ from 'lodash';
import { v4 as uuidv4 } from 'uuid';
import '../App.css';
import { Fragment } from 'react';

const Comp = ({ comp, unitDistance, onClick }) => {
  return <div style={{ minHeight: '68px' }}>
    {_.chain(comp)
      .orderBy(unit => unitDistance[unit], "desc")
      .map(unit => (onClick) ?
        <img onClick={() => onClick(unit)} key={`${unit}-img`} alt={unit} src={`icons/units/${unit}.png`} />
        :
        <img key={`${unit}-img`} alt={unit} src={`icons/units/${unit}.png`} />
      )
      .value()}
  </div>
}

const Matchup = () => {
  const suggestionRef = useRef();
  const selectionRef = useRef();

  const [showLose, setShowLose] = useState(false);
  const [matchup, setMatchup] = useState([]);
  const [unitDistance, setUnitDistance] = useState({});
  const [blacklist, setBlacklist] = useState({});

  const [sortedMatchup, setSortedMatchup] = useState([]);

  const [bestComp, setBestComp] = useState([])

  const [winningMatches, setWinningMatches] = useState([]);
  const [losingMatches, setLosingMatches] = useState([]);
  const [defCharOptions, setDefCharOptions] = useState([]);
  const [attackOptions, setAttackOptions] = useState([]);

  const [selectedDefender, setSelectedDefender] = useState([]);
  const [unitMatchups, setUnitMatchups] = useState({})

  useEffect(() => {
    fetch('data/matchup.json')
      .then((r) => r.json())
      .then((json) => setMatchup(json));

    fetch('data/unit_distance.json')
      .then((r) => r.json())
      .then((json) => setUnitDistance(json));

    fetch('data/blacklist.json')
      .then((r) => r.json())
      .then((json) => setBlacklist(json));
  }, []);

  useEffect(() => {
    const sorted = _.chain(matchup)
      .uniqWith(_.isEqual)
      .map(matchup => {
        return {
          ...matchup,
          winner: _.orderBy(matchup['winner'], unit => unitDistance[unit], "desc"),
          loser: _.orderBy(matchup['loser'], unit => unitDistance[unit], "desc"),
        };
      })
      .value();

    const unitMatchups = {};
    matchup.forEach(match => {
      match.loser.forEach(loseUnit => {
        match.winner.forEach(winUnit => {
          unitMatchups[loseUnit] = { ...unitMatchups[loseUnit] }
          unitMatchups[loseUnit][winUnit] = (unitMatchups[loseUnit][winUnit]) ? unitMatchups[loseUnit][winUnit] + 1 : 1;
        })
      })
    })

    setUnitMatchups(unitMatchups);
    setSortedMatchup(sorted)
  }, [matchup, unitDistance]);

  useEffect(() => {
    if (sortedMatchup.length === 0) return;

    const result = _.chain(sortedMatchup)
      .countBy(matchup => matchup.winner)
      .entries()
      .orderBy(1, "desc")
      .map(entry => {
        const comp = entry[0].split(',');
        const wins = entry[1];
        const losses = _.chain(matchup).filter(matchup => _.isEqual(comp, matchup.loser)).value().length;
        const key = uuidv4();
        return { comp, wins, losses, key }
      })
      .chunk(10)
      .value();
    if (result.length > 0) { setBestComp(result[0]) } else { setBestComp([]) };

    setDefCharOptions(_.chain(sortedMatchup).map(matchup => matchup.loser).flatten().uniqWith(_.isEqual).sort().value())
    setAttackOptions(_.chain(sortedMatchup).map(matchup => matchup.winner).flatten().uniqWith(_.isEqual).filter(unit => !_.includes(blacklist, unit)).value())

  }, [sortedMatchup, blacklist, matchup]);

  useEffect(() => {
    const result = _.chain(sortedMatchup)
      .filter(matchup => _.intersection(matchup.loser, selectedDefender).length >= 1)
      .filter(matchup => _.intersection(matchup.winner, blacklist).length <= 0)
      .filter(matchup => matchup.winType === undefined || matchup.winType === 'kill')
      .map(winningMatch => {
        const similarity = _.intersection(winningMatch.loser, selectedDefender).length;
        const wins = _.chain(matchup).filter(matchup => _.isEqual(winningMatch.winner, matchup.winner) && _.isEqual(winningMatch.loser, matchup.loser)).value().length;
        const losses = _.chain(matchup).filter(matchup => _.isEqual(winningMatch.winner, matchup.loser) && _.isEqual(winningMatch.loser, matchup.winner)).value().length;
        const winner = [...winningMatch.winner];
        const loser = [...winningMatch.loser];
        return { winner, loser, similarity, wins, losses };
      })
      .orderBy(matchup => [matchup.similarity, matchup.wins, matchup.losses], ["desc", "desc", "asc"])
      .uniqWith(_.isEqual)
      .map(matchup => {
        const key = uuidv4();
        return { ...matchup, key }
      })
      .chunk(4)
      .value();
    if (result.length > 0) { setWinningMatches(result[0]) } else { setWinningMatches([]) };
  }, [selectedDefender, sortedMatchup, blacklist, matchup]);

  useEffect(() => {
    const result = _.chain(sortedMatchup)
      .filter(matchup => _.intersection(matchup.winner, selectedDefender).length >= 2)
      .map(losingMatch => {
        const similarity = _.intersection(losingMatch.winner, selectedDefender).length;
        return { winner: [...losingMatch.winner], loser: [...losingMatch.loser], similarity };
      })
      .uniqWith(_.isEqual)
      .map(matchup => {
        const key = uuidv4();
        return { ...matchup, key }
      })
      .orderBy(matchup => matchup.similarity, "desc")
      .chunk(15)
      .value();
    if (result.length > 0) { setLosingMatches(result[0]) } else { setLosingMatches([]) };
  }, [selectedDefender, sortedMatchup]);

  const onDefClick = (name) => {
    if (selectedDefender.includes(name)) {
      setSelectedDefender([...selectedDefender.filter(defender => defender !== name)])
    } else if (selectedDefender.length < 5) {
      setSelectedDefender(_.chain([...selectedDefender, name]).uniqWith(_.isEqual).value())
    }
  }

  const onClearClick = () => {
    setSelectedDefender([])
  }

  const removeUnitHandler = (unit) => {
    setSelectedDefender(selectedDefender.filter(defender => defender !== unit))
  }

  const onClickShowLose = () => {
    setShowLose(!showLose);
  }

  const scrollTo = (target) => {
    target.current.scrollIntoView()
  }

  return (
    <div className="content">
      <div ref={selectionRef} className='selection-container'>

        <div>
          <div>Selected Defenders</div>
          <Comp onClick={removeUnitHandler} comp={selectedDefender} unitDistance={unitDistance} />
          <div>
            <button style={{ height: "50px", width: '50%' }} onClick={onClearClick}>Clear team</button>
            <button style={{ height: "50px", width: '50%' }} onClick={() => scrollTo(suggestionRef)}>Jump to teams</button>
          </div>
        </div>

        <div>
          <div>Select</div>
          <div className="defButtons">
            {defCharOptions.map(name => (
              <div key={`${name}-icon`} className="button-container" onClick={onDefClick.bind(this, name)} >
                <img className={selectedDefender.includes(name) ? "unit-button-selected" : ""} alt={name} src={`icons/units/${name}.png`} />{name}
              </div>))}
          </div>
        </div>

        {/* <div>
          <div>Available Units</div>
          <Comp comp={attackOptions} unitDistance={unitDistance} />
        </div> */}

      </div>

      <div ref={suggestionRef} className="suggestion-container">
        <button style={{ height: "50px" }} onClick={() => scrollTo(selectionRef)}>Back to top</button>
        {(!showLose) ?
          <Fragment>
            <h4 onClick={onClickShowLose}>Winning matchups</h4>
            <div className="wins">
              {winningMatches.map(comps => (
                <div key={comps.key} className="comp-container">
                  <div>
                    <div>Winner: W/L: {comps.wins}/{comps.losses} ({parseInt(100 * comps.wins / (comps.losses + comps.wins))}%)</div>
                    <Comp comp={comps.winner} unitDistance={unitDistance} />
                  </div>
                  <div>
                    <div>Loser: Same Units: {comps.similarity}</div>
                    <Comp comp={comps.loser} unitDistance={unitDistance} />
                  </div>
                </div>
              ))}
            </div>
          </Fragment>
          :
          <Fragment>
            <h4 onClick={onClickShowLose}>Losing matchups</h4>
            <div className="loses">
              {losingMatches.map(comps => (
                <div key={comps.key} className="comp-container">
                  <div>
                    <div>Winner(Same Units: {comps.similarity})</div>
                    <Comp comp={comps.winner} unitDistance={unitDistance} />
                  </div>
                  <div>
                    <div>Loser</div>
                    <Comp comp={comps.loser} unitDistance={unitDistance} />
                  </div>
                </div>
              ))}
            </div>
          </Fragment>
        }

        {winningMatches.length === 0 &&
          bestComp.map(comp => (
            <div key={comp.key} className="comp-container">
              <div>W/L: {comp.wins}/{comp.losses} ({parseInt(100 * comp.wins / (comp.losses + comp.wins))}%)</div>
              <Comp comp={comp.comp} unitDistance={unitDistance} />
            </div>
          ))
        }

      </div>

    </div>
  );
}

export default Matchup;
